<template>
  <el-row>
    <el-col :span="24">
      <CenterNav></CenterNav>
      <el-row class="boxContent">
        <el-col :span="24">
          <el-table
            :data="tableData"
            style="width: 100%"
            border
            :default-sort="{prop: 'date', order: 'descending'}"
          >
            <el-table-column
              prop="name"
              label="姓名"
              sortable
              width="200">
            </el-table-column>
            <el-table-column
              prop="info"
              label="产品"
              sortable
              width="250">
            </el-table-column>
            <el-table-column
              label="自定义内容"
              width="150">
              <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.info[0] }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="自定义显示"
              width="180">
              <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                  <p>姓名: {{ scope.row.name }}</p>
                  <p>住址: {{ scope.row.info[0] }}</p>
                  <div slot="reference" class="name-wrapper">
                    <el-tag size="medium">{{ scope.row.info[1] }}</el-tag>
                  </div>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index, scope.row)">编辑
                </el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>
<script>
import {getConfigId} from '../../api/index/request'
import CenterNav from '../public/CenterNav'

export default {
  components: {CenterNav},
  data () {
    return {
      tableData: null,
      info: null
    }
  },
  mounted () {
    getConfigId()
      .then(response => {
        this.tableData = response.data.sites
        this.info = this.tableData.map((e) => {
          return e.info
        })
        console.log(this.tableData)
      })
    // axios的get请求
    // this.$axios.get('/try/ajax/json_demo.json')
    //   .then(response => (this.info = response))
    //   .catch((error) => {
    //     console.log(error)
    //   })
  },
  methods: {
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
    }
  }
}
</script>
<style lang="scss" scoped>
.el-button {
  margin: 0;
}
.boxContent {
  padding: 15px;
  background-color: white;
}
</style>



