<template>
  <div class="hello">
    <el-container>
      <el-aside :style="{width: asideWidth}">
        <Navtiem :isCollapse="isCollapse"></Navtiem>
      </el-aside>
      <el-container>
        <el-header>
          <el-radio-group v-model="isCollapse">
            <el-radio-button :label="false">展开</el-radio-button>
            <el-radio-button :label="true">收起</el-radio-button>
          </el-radio-group>
          <h3>Header</h3>
          <el-button type="primary" class="el-icon-error" :loading="isLogOut" @click="logOut">LogOut</el-button>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
        <el-footer>footer</el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Navtiem from './Navtiem'
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      isCollapse: false,
      asideWidth: '150px',
      isLogOut: false
    }
  },
  components: {Navtiem},
  mounted () {
    this.$vueEvent.$on('message', (el) => {
      console.log(el)
    })
  },
  beforeUpdate () {
    if (this.$data.isCollapse === false) {
      this.$data.asideWidth = '150px'
    } else {
      this.$data.asideWidth = '65px'
    }
    // console.log(this.isCollapse)
  },
  methods: {
    logOut () {
      this.isLogOut = true
      setTimeout(() => {
        this.$router.push({ path: '/' })
      }, 2000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.hello {
  height: 100%;
  .el-aside {
    position: relative;
  }
  .el-container {
    min-width: 400px;
    height: 100%;
  }
  .el-header,
  .el-footer {
    background-color: white;
    color: #333;
    text-align: center;
    line-height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .el-aside {
    background-color: #d3dce6;
    color: #333;
    text-align: center;
    line-height: 200px;
    overflow-y: hidden;
    overflow-x: hidden;
  }
  .el-main {
    background-color: #e9eef3;
    color: #333;
    text-align: center;
    padding: 0;
  }
  body > .el-container {
    margin-bottom: 40px;
    font-size: 14px;
  }
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .el-menu {
    border-right-width: 0;
  }
  .el-menu {
    overflow: hidden;
  }
  // .el-radio-group {
  //   position: absolute;
  //   left: 10px;
  //   top: 10px;
  // }
  .el-submenu__title {
    padding-left: 0 !important;
  }
  .el-menu-item {
    padding: 0 60px 0 0 !important;
    padding-left: 0 !important;
  }
  // .el-button {
    // position: absolute;
    // right: 10px;
    // top: 10px;
  // }
  .el-icon-error:before {
    padding-right: 5px;
  }
}
</style>



