<template>
  <div>
    <div class="user">
      <el-avatar :src="circleUrl"></el-avatar>
    </div>
    <el-row>
      <el-col :span="24">
        <el-menu :default-active="$route.path" class="el-menu-vertical-demo" :collapse="isCollapse" :unique-opened="isUnique"
                 :collapse-transition="isTransition" router>
          <el-menu-item index="0" :index="container.name">
            <i class="el-icon-menu"></i>
            <span slot="title">{{container.navItem}}</span>
          </el-menu-item>
          <el-submenu :index="index.toString()" v-for="(item, index) in this.router" :key="index">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">{{item.name}}</span>
            </template>
            <el-menu-item-group v-for="(itemNav, indexTo) in item.children" :key="indexTo">
              <el-menu-item :index="itemNav.path">{{itemNav.name}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          
        </el-menu>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  props: ['isCollapse'],
  data () {
    return {
      circleUrl: require('@/assets/img/user.png'),
      container: {name: '/home', navItem: '首页'},
      isTransition: false,
      isUnique: true,
      router: ''
    }
  },
  mounted() {
    this.router = this.$router.options.routes;
    console.log(this.router)
  },
  methods: {
    handleOpen (key, keyPath) {
      // console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      // console.log(key, keyPath)
    }
  },

  beforeUpdate () {
    // console.log(this)
  }
}
</script>
<style lang="scss" scoped>
.user {
  height: 150px;
}
</style>



