import Vue from 'vue'
import Router from 'vue-router'
import Container from '../components/index/Container.vue'
import Login from '../components/Login/Login.vue'
import homePage from '../components/index/homepage.vue'
import Active from '../components/column/Active.vue'
import Table from '../components/column/Table.vue'
import Component from '../components/column/Component.vue'
import Dasha from '../components/index/dasha'
import hometest from '../components/index/hometest.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: '首页',
      component: Container,
      children: [
        {
          path: '/home',
          name: '首页',
          component: homePage
        }
      ]
    },
    {
      path: '/',
      name: '登录',
      component: Login,
    },
    {
      path: '/index',
      name: '内容',
      component: Container,
      children: [
        {
          path: '/dasha',
          name: '默认页面',
          component: Dasha
        },
        {
          path: '/active',
          name: '主页',
          component: Active
        },
        {
          path: '/table',
          name: '表单',
          component: Table
        },
        {
          path: '/component',
          name: '组件',
          component: Component
        }
      ]
    },
    {
      path: '/fix',
      name: '内容',
      component: Container,
      children: [
        {
          path: '/hometest',
          name: '默认页面',
          component: hometest
        },
        {
          path: '/active',
          name: '主页',
          component: Active
        },
        {
          path: '/table',
          name: '表单',
          component: Table
        },
        {
          path: '/component',
          name: '组件',
          component: Component
        }
      ]
    }
  ]
})



