import {Loading} from 'element-ui'

export default {
  setLoading() {
    const loading = Loading.service({
      lock: true,
      text: 'Loading',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 1)'
    })
    setTimeout(() => {
      loading.close()
    },2000)
  }
}







