<template>
  <div class="login">
    <div class="demo-image__placeholder">
      <div class="block">
        <el-image :src="require('@/assets/img/login.jpeg')">
          <div slot="placeholder" class="image-slot">
            加载中
            <span class="dot">...</span>
          </div>
        </el-image>
      </div>
    </div>
    <h2>{{ $store.state.name }}</h2>
    <div class="input">
      <el-form
        :model="valiForm"
        :rules="rules"
        ref="valiForm"
        label-width="100px"
        class="demo-valiForm"
      >
        <el-form-item prop="name">
          <el-input placeholder="请输入用户名" v-model="valiForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input placeholder="请输入密码" v-model="valiForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" plain :disabled="isLogIn">{{logIn}}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>

export default {
  data () {
    let valiDatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('密码不能为空'))
      }
    }
    return {
      valiForm: {
        name: '',
        password: ''
      },
      rules: {
        name: [
          { required: true, message: '用户名不能为空', trigger: 'blur' }
        ],
        password: [
          { validator: valiDatePass, trigger: 'blur' }
        ]
      },
      logIn: '登录',
      isLogIn: false
    }
  },
  methods: {
    onSubmit () {
      if (this.valiForm.name && this.valiForm.password) {
        this.isLogIn = true
        this.$common.setLoading()
        /* 由于页面初始化，container组件并没有创建，在第一次发出$emit时会无法监听到
           使用$nextTick在container组件Dom更新后再执行发出$emit回调
        */
        this.$nextTick(() => {
          console.log('下次dom更新了')
          this.$vueEvent.$emit('message', '成都')
        })
        this.$router.push({ path: '/index' })
      } else {
        this.open()
      }
    },
    open () {
      const h = this.$createElement
      this.$notify({
        title: 'Error',
        message: h('i', { style: 'color: white' }, '请输入正确的字段'),
        duration: 2000
      })
    }
  },
  beforeDestroy() {
    this.$vueEvent.$off('message')
  },
}
</script>
<style lang="scss" scoped>
.login {
  margin-top: 100px;
  h2 {
    text-align: center;
  }
  .el-image {
    width: 300px;
    display: block;
    margin: 0 auto;
  }
  .input {
    width: 300px;
    margin: 0 auto 20px;
    .el-input {
      width: 300px;
    }
    .el-form-item {
      margin-left: -100px;
    }
    .el-form-item:last-child {
      text-align: center;
    }
  }
}
</style>



